import { SocketConnection } from './socket';
import { FeedReader } from './feedreader';

export class EmergencyFeed {
  constructor(options = {}) {
    this.socket = new SocketConnection();
    this.reader = new FeedReader();
    
    this.serviceDisruption
    = this.emergencyMessage
    = this.weatherAlert
    = () => {};

    this.addSocketListeners();
  }
  addSocketListeners() {
    this.socket.onMessage(event => {
      const eventData = JSON.parse(event.data);
      switch (eventData.type) {
        case 'disruption':
          const serviceDisruptions = this.reader.parseServiceDisruptions(eventData.data);
          this.serviceDisruption(serviceDisruptions);
          break;
        case 'emergency':
          const emergencyMessages = this.reader.parseEmergencyMessages(eventData.data);
          this.emergencyMessage(serviceDisruptions);
          break;
        case 'weather':
          const weatherAlerts = this.reader.parseWeatherAlerts(eventData.data);
          this.weatherAlert(serviceDisruptions);
          break;
      }
    });
  }
  onServiceDisruption(callback) {
    this.serviceDisruption = callback;
  }
  onEmergencyMessage(callback) {
    this.emergencyMessage = callback;
  }
  onWeatherAlert(callback) {
    this.weatherAlert = callback;
  }
}


