export class FeedReader {
  constructor() {
    this.months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
  }
  parseServiceDisruptions(data) {
    const channelItems = data.rss.channel[0].item;
    return channelItems.map(item => {
      const {
        title, description, link,
        guid, summary, category,
        article, pubDate
      } = item;
      let media = item['media:content'] || null;
      if (media && media.length) {
        const [{
          $: { url: mediaContent, description: mediaDescription }
        }] = media;
        media = { mediaContent, mediaDescription };
      }
      return {
        title, description, link,
        guid, summary, category,
        pubDate: this.formatDate(pubDate),
        article, ...media || {}
      }
    });
  }
  formatDate(dateString) {
    const date = new Date(dateString);
    return [
      this.months[date.getMonth()],
      ' ', date.getDate(),
      ', ', date.getFullYear(),
      ' ', this.leadingZero(date.getHours()),
      ':', this.leadingZero(date.getMinutes())
    ].join('');
  }
  leadingZero(number) {
    return number < 10 ? `0${number}` : number;
  }
  pluck(obj, props) {
    // return Object.assign({}, ...props.map(prop => ({[prop]: obj[prop]})));
    return props.reduce((result, key) => { result[key] = orig[key]; return result; }, {});
  }
}



