export class SocketConnection {
  constructor(url) {
    if (!url) {
      url = `ws://${ window.document.location.host.replace(/:.*/, '') }:8080`; //dev
      // url = `wss://${ window.document.location.host.replace(/:.*/, '') }`; //live
    }
    this.connection = new WebSocket(url);
  }
  onMessage(callback) {
    this.connection.onmessage = callback;
  }
}

export class SocketConnectionDisplay {
  setData(id, value) {
    document.getElementById(id).innerHTML = value;
  }
  setClass(id, value) {
    var el = document.getElementById(id);
    el.className = 'label';
    el.classList.add(value);
  }
  receiveDataChange(data) {
    // this.setClass('status', data.class);
    // this.setData('status', data.status);
    // this.setData('message', data.message);
    // this.setData('alert', `<div class="callout ${data.class}" data-animate="slide-in-down" data-closable="slide-out-up">
    //   <strong>This is Important!</strong><br/>
    //   The status has changed to: <em>${data.status}</em>
    //   <button class="close-button" aria-label="Dismiss alert" type="button" data-close>
    //     <span aria-hidden="true">&times;</span>
    //   </button>
    // </div>`)
    console.info(data);
  }
}



